import React, { Component } from "react";
import { lineRadial, curveBasisClosed } from "d3-shape";

function roundPath(path, precision) {
  if (!path) return;
  const query = /[\d\.-][\d\.e-]*/g;
  return path.replace(
    query,
    n => Math.round(n * (1 / precision)) / (1 / precision)
  );
}

class IndexPage extends Component {
  state = {
    line:
      "M119,-75C163,-41,212,9,204,48C196,86,132,113,79,123C25,133,-16,127,-58,111C-99,94,-140,68,-162,22C-184,-24,-188,-89,-158,-119C-128,-150,-64,-147,-13,-137C38,-126,76,-109,119,-75Z",
    complexity: 10,
    minRadius: 120,
    radius: 300,
    palette: ["gold", "red"],
    angle: 110,
    contrast: 0.5,
    url: "",
    mounted: false,
    bypassAnimation: false
  };
  static defaultProps = {
    width: 600,
    height: 600,
    fullCircle: 2 * Math.PI
  };
  componentDidMount() {
    this.setState({ mounted: true });
    this.updateShape();
    window.addEventListener("message", this.onWindowMessage);
  }
  componentWillUnmount() {
    window.removeEventListener("message", this.onWindowMessage);
  }
  onWindowMessage = event => {
    if (event.data.type === "render") {
      const { complexity, palette, angle, contrast } = event.data.payload;
      this.setState({
        complexity,
        palette,
        angle,
        minRadius: this.state.radius * (1 - contrast / 10)
      });
      this.updateShape();
    } else if (event.data.type === "download") {
      window.parent.postMessage(
        { type: "download", image: this.state.url, imageType: "svg" },
        "*"
      );
    }
  };
  generateShape = complexity => {
    const { fullCircle } = this.props;
    return lineRadial()
      .radius(d => d.radius)
      .angle((d, i) => (fullCircle / complexity) * i)
      .curve(curveBasisClosed);
  };
  updateShape = custom => {
    const { radius } = this.state;

    const bypassAnimation = custom
      ? custom.complexity !== this.state.complexity
      : false;

    const minRadius = custom ? custom.minRadius : this.state.minRadius;
    const contrast = custom ? custom.contrast : this.state.contrast;
    const complexity = custom ? custom.complexity : this.state.complexity;
    const color = custom ? custom.color : this.state.color;

    const sampleData = Array(complexity)
      .fill()
      .map(() => ({
        radius: minRadius + Math.floor(Math.random() * (radius - minRadius))
      }));

    const newLine = this.generateShape(complexity)(sampleData) + "Z";

    const url = this.generateDownloadUrl(newLine);

    this.setState(
      {
        contrast,
        minRadius,
        complexity,
        color,
        line: newLine,
        bypassAnimation,
        url
      },
      () => {
        this.setState({ bypassAnimation: false });
      }
    );
  };
  updateColor = color => {
    this.setState({
      color: color.hex,
      url: this.generateDownloadUrl(this.state.line)
    });
  };
  updateComplexity = val => {
    this.updateShape({
      contrast: this.state.contrast,
      minRadius: this.state.minRadius,
      complexity: val,
      color: this.state.color
    });
  };
  updateContrast = val => {
    this.updateShape({
      contrast: val,
      minRadius: this.state.radius * (1 - val),
      complexity: this.state.complexity,
      color: this.state.color
    });
  };
  generateDownloadUrl = newLine => {
    const width = window.innerWidth;
    const height = window.innerHeight;
    const { palette, angle } = this.state;

    const svgTree = `<?xml version="1.0" standalone="no"?>
    <svg
    width="${width}"
    height="${height}"
    viewBox="0 0 600 600"
    xmlns="http://www.w3.org/2000/svg"
  >
    <linearGradient id="Gradient1" gradientTransform="rotate(${angle})">
      <stop class="stop1" offset="0%" stop-color="${palette[0]}"></stop>      
      <stop class="stop2" offset="100%" stop-color="${palette[1]}"></stop>
    </linearGradient>
    <g transform="translate(${width / 2},${height / 2})">
      <path d="${newLine}" fill="url('#Gradient1')" />
    </g>
  </svg>`;

    return "data:image/svg+xml;charset=utf-8," + encodeURIComponent(svgTree);
  };
  render() {
    const { line, palette, angle } = this.state;

    const width = window.innerWidth;
    const height = window.innerHeight;

    return (
      <>
        <svg
          width={width}
          height={height}
          viewBox={`0 0 570 570`}
          xmlns="http://www.w3.org/2000/svg"
        >
          <linearGradient id="Gradient1" gradientTransform={`rotate(${angle})`}>
            <stop class="stop1" offset="0%" stop-color={palette[0]} />
            <stop class="stop2" offset="100%" stop-color={palette[1]} />
          </linearGradient>
          <g transform={`translate(285,285)`}>
            <path d={`${line}`} fill="url('#Gradient1')" />
          </g>
        </svg>
      </>
    );
  }
}

export default IndexPage;
